          p.mb-cus La mquina de coser es uno de los inventos ms representativos de la historia, cuya evolucin ha sido significativa gracias a la incorporacin de electricidad, rodamientos, metales y otros suministros que han mejorado su eficiencia y velocidad. Hasta hace 25 aos, los equipos de esa poca difcilmente podan considerarse verdaderas mquinas de coser, ya que su rendimiento apenas alcanzaba el 12 % del tiempo total de costura, mientras que el 88 % restante corresponda a movimientos manuales. Debido a su bajo nivel de automatizacin, estos dispositivos eran ms bien herramientas de costura que mquinas propiamente dichas.
