      .row.justify-content-center.mb-5
        .col-sm-12.col-lg-11
            p.mb-0 Actualmente, se estn desarrollando mquinas de coser por ultrasonido, las cuales incorporan un sonotrodo que emite ondas ultrasnicas de 60.000 ciclos contra una rueda transportadora con forma de puntada. Este sistema permite soldar las telas en lugar de coserlas y, al cambiar el disco, puede imitar diferentes tipos de costuras, como las de mquina plana, fileteadora, recubridora e incluso ojales con corte central. Para un ptimo resultado, el material a unir debe contener al menos un 60 % de polister, garantizando una soldadura resistente y de alta calidad.
