          img(src='@/assets/curso/temas/t1/4.svg', alt='Mquina de coser industrial de diseo clsico, presenta una estructura metlica robusta con una aguja en la parte frontal, un carrete de hilo en la parte superior y una rueda de mano en el lateral derecho.')

