          p.mb-0 En la primera generacin (tecnologa convencional), el operario realiza el 83 % de las tareas manualmente. En la segunda generacin (tecnologa programable), la intervencin manual se reduce al 60 %, mejorando la automatizacin. En la tercera generacin (tecnologa robtica), la automatizacin es mxima, mejorando la apariencia de las prendas y permitiendo que un solo operario supervise hasta tres mquinas, aumentando la eficiencia y reduciendo la carga de trabajo manual. 
