            div(titulo='Tecnologa programable o electrnica')
              .row.mb-0
                .col-12
                  p.mb-4 Integra sistemas electroneumticos que reducen el esfuerzo del operario al 60 %. A travs de un tablero de control, se programan la velocidad y ciertos movimientos, optimizando el consumo de hilo y reduciendo la fatiga. Ejemplo: mquina pegadora de bolsillos en jeans.
