                      li.d-flex
                          i.fas.fa-check-circle.bg-color-1
                          p.mb-0 Programacin de ciclos de costura (ej. pegar marquilla o bolsillo).
                  p.mb-0 Los sistemas neumticos son activados por el aire a presin conducido a travs de mangueras,logrando complementar los movimientos de los sistemas electrnicos como el corte de hilo o succin de residuos, entre otros.
            div(titulo='Tecnologa robtica')
              .row.mb-0
                .col-12
                  p.mb-3 La tecnologa robtica en confeccin comprende mquinas diseadas para realizar todo el ciclo de una operacin de manera automtica. Ejemplos de estas mquinas incluyen la dobladilladora de ruedos (recubridora), la presilladora para cajas de perilla y la dobladora de camisetas. Su uso reduce hasta un 30 % los movimientos del operario.
                  p.mb-0 Este tipo de tecnologa se emplea en procesos como el pegado de marquillas en pantalones, la ojaladora, la pretinadora, la presilladora y la fileteadora industrial, disminuyendo el esfuerzo del trabajador en un 60% y mejorando la eficiencia en la produccin.
