                  p.mb-0 Compuesto por dos discos entre los cuales pasa el hilo, permitiendo graduar su tensin. La regulacin se realiza mediante un tornillo de mano que, adems, cuenta con un resorte que amortigua la fuerza ejercida por la palanca tira-hilo.

