                  p.mb-0 Es la que sostiene la aguja en su extremo y permite que la aguja suba y baje. Posee un tornillo que permite el ingreso de la aguja y la asegura. En algunas mquinas el orificio dentro del cual se instala la aguja no es circular, sino que tiene una parte plana que debe coincidir con la parte plana de la aguja.
