                        p.mb-0 Para telas delgadas, se recomienda aplicar poca presin, mientras que para telas ms gruesas es necesario aumentarla. Si el pie prensatela no est correctamente orientado, la aguja puede golpearlo, doblarse o romperse, lo que podra causar lesiones.
