            h4.text-center Mquina plana de 2 agujas
            p.mb-4.text-center Esta mquina duplica la puntada de la plana de una aguja, lo que permite ahorrar tiempo en ciertas operaciones. Es ampliamente utilizado en la confeccin de ropa interior, especialmente en la fabricacin de brasieres.
