                p.mb-4 Este tipo de mquina es ampliamente utilizado en la confeccin de ropa interior debido a la elasticidad de su punta y su versatilidad en diversas operaciones. Se emplea tanto en entornos familiares como industriales.
                p.mb-4 A diferencia de la mquina plana, la mquina en zigzag cuenta con un mecanismo que permite a la aguja moverse de izquierda a derecha en cada punta, generando el caracterstico movimiento en zigzag.
                p.mb-4 Existen varios tipos de zigzag, siendo los ms utilizados el Z1 (zigzag sencillo) y el Z3 (zigzag triple). Es ideal para trabajar con tejidos ligeros y medianos.
                p.mb-0.mb-cus Se utiliza en la colocacin de resortes en lencera, bordados y diversas aplicaciones, como la instalacin de cierres, la unin de copas de guata, la fijacin de elsticos y abrochaduras, el asentado de base elstica, la unin de copas base y la confeccin de acabados tipo rollito.
