      .bg-color-6.mb-4(data-aos="fade-left")
        h3.px-3.py-1 Regulacin de la tensin del hilo de la aguja
      
      p.mb-3 Es fundamental que la tensin del hilo de la aguja sea adecuada para garantizar una puntada precisa. En este sentido, pueden presentarse los casos:
      .bg-full-width.bg-fondo-1.mb-0.pt-5.mb-cus
          .px-4.px-md-5.pb-md-3
            .row.justify-content-center.mb-0
              .col-sm-12.col-lg-4.d-none.d-lg-block
                img(src='@/assets/curso/temas/t1/26.png', alt='')
              .col-sm-12.col-lg-8
                TabsA.color-acento-botones
                  .tarjeta.color-acento-botones.p-4(titulo="Exceso de tensin")
                    h4 Exceso de tensin
                    p.mb-4 Ocurre cuando el hilo de la aguja est demasiado ajustado, lo que provoca que quede completamente visible en la parte superior de la tela, ya que ejerce ms fuerza que el hilo de la bobina.
                    .row.mb-0
                      .col-sm-12.col-lg-8
                        .titulo-sexto.color-primario
                          p.mb-0 #[b Figura 14.] Exceso de tensin  del hilo 
                        figure.mb-0
                          img.w-95.m-auto(src='@/assets/curso/temas/t1/27.png', alt='Ilustracin que presenta una puntada con tensin desequilibrada en una mquina de coser. El hilo superior ejerce demasiada presin, lo que hace que el hilo de la bobina quede visible en la parte inferior de la tela, indicando un exceso de tensin en el hilo de la aguja.')
                          figcaption.mb-0 Fuente: SENA, (2014). 

                  .tarjeta.color-acento-botones.p-4(titulo="Poca tensin")
                    h4 Poca tensin
                    p.mb-4 Cuando la tensin del hilo de la aguja es muy dbil, no logra halar el hilo de la bobina hacia el centro del material. Por lo tanto, el hilo de la bobina se ver completamente por la parte inferior de la tela.
                    .row.mb-0
                      .col-sm-12.col-lg-8
                        .titulo-sexto.color-primario
                          p.mb-0 #[b Figura 15.] Poca tensin  del hilo
                        figure.mb-0
                          img.w-95.m-auto(src='@/assets/curso/temas/t1/28.png', alt='Ilustracin que presenta una puntada con poca tensin en el hilo de la aguja. Se observa que el hilo de la bobina predomina en la superficie de la tela, formando lazos sueltos y una costura deficiente que puede deshacerse fcilmente.')
                          figcaption.mb-0 Fuente: SENA, (2014).

                  .tarjeta.color-acento-botones.p-4(titulo="Tensin correcta")
                    h4 Tensin correcta
                    p.mb-4 Cuando la tensin del hilo de la aguja es la adecuada, el entrelazamiento de los hilos queda en la mitad de la tela.
                    .row.mb-0
                      .col-sm-12.col-lg-8
                        .titulo-sexto.color-primario
                          p.mb-0 #[b Figura 16.] Tensin correcta  del hilo
                        figure.mb-0
                          img.w-95.m-auto(src='@/assets/curso/temas/t1/29.png', alt='Ilustracin que representa una punta con tensin equilibrada en una mquina de coser. El hilo de la aguja y el hilo de la bobina se entrelazan correctamente en el interior de la tela, logrando una costura uniforme y resistente.')
                          figcaption.mb-0 Fuente: SENA, (2014).  

