      .row.justify-content-center.mb-5
        .col-sm-12.col-lg-6
          LineaTiempoD.color-primario
            .row(numero="1" titulo="Lubricacin")
              .col-12
                p.mb-0 Manual y automtica.
            .row(numero="2" titulo="Sistemas de alimentacin")
              .col-12
                p.mb-0 El transporte del material puede ser oscilante sencillo de cuatro tiempos o pasos, o una combinacin de oscilante sencillo con diferencial.
            .row(numero="3" titulo="Hilo utilizado")
              .col-12
                p.mb-0 Se emplean hilos de fibras naturales, sintticas y artificiales en los calibres adecuados para el material a confeccionar. Se recomienda el uso de hilos con ncleo de polister, ya que ofrecen una elongacin del 17 % al 20 %, lo que proporciona mayor elasticidad y tolerancia en la costura. Adems, es fundamental considerar que los hilos lubricados mantengan una tensin constante, reduciendo la cantidad de estiramiento necesario durante la costura.
            .row(numero="4" titulo="Sistemas de aguja")
              .col-12
                p.mb-0 Se recomienda el uso de agujas de bola, ya que su diseo permite un deslizamiento suave entre las telas, evitando daos como perforaciones o roturas.
        .col-sm-12.col-lg-6.d-none.d-lg-block
