          .bg-color-7.p-4.justify-content-center.align-items-center
            p.mb-3 Para una mejor comprensin, se tomar como referencia el movimiento del gancho, el cual gira en sentido contrario a las manecillas del reloj, y su punta ser el punto de orientacin.
            p.mb-0 El proceso comienza cuando la aguja desciende desde su punto muerto superior, llevando el hilo a travs del material hasta alcanzar su punto ms bajo. En ese instante, la punta del gancho se ubica aproximadamente a las 2 en punto. Mientras tanto, la palanca tira-hilos desciende para suministrar hilo a la aguja, y los dientes de arrastre se encuentran en la parte inferior de la planchuela, movindose en direccin hacia el operario.
        .col-sm-12.col-lg-4
          .titulo-sexto.color-primario
            p.mb-0 #[b Figura 17.] Mecanismo de formacin de la puntada
          figure.mb-0
            img.w-80.m-auto(src='@/assets/curso/temas/t1/32.png', alt='Ilustracin del mecanismo de formacin de la puntada en una mquina de coser. Se muestra la direccin de rotacin del gancho en sentido contrario a las manecillas del reloj, con la punta del gancho capturando el hilo de la aguja para formar la punta. Adems, se destacan los componentes clave como la planchuela y el sistema de alimentacin del material.')
            figcaption.mb-0 Fuente: SENA, (2014). 

      .row.justify-content-center.mb-5.bg-color-8.p-4
        .col-sm-12.col-lg-3.mb-cus
          p.mb-0 La punta del gancho captura la lazada y la posicin en el taln del gancho o en la ranura de alojamiento de la lazada. A partir de este punto, la referencia ser la lazada ubicada en el taln del gancho. Cuando esta alcanza aproximadamente la posicin de las 11 en el reloj, el desviador de hilo o deflector comienza a abrir la lazada, impidiendo que se enrede con la caja de la bobina. Mientras tanto, la aguja se retira del material, la palanca sigue descendiendo y el diente empieza a emerger de la planchuela, invirtiendo su movimiento.
        .col-sm-12.col-lg-4
          .titulo-sexto.color-primario
            p.mb-0 #[b Figura 18.] Formacin de la puntada (etapas del ciclo de la costura)
          figure.mb-0.mb-cus
            img.w-80.m-auto(src='@/assets/curso/temas/t1/33.png', alt='Ilustracin del proceso de formacin de la puntada en una mquina de coser. Se muestra la direccin de rotacin del gancho y la interaccin entre la aguja, el hilo y la bobina en diferentes etapas del ciclo de costura.')
            figcaption.mb-0 Fuente: SENA, (2014). 
        .col-sm-12.col-lg-5
          p.mb-3 Se dispone a ascender tirando el hilo de la lazada para comenzar a sacarla del gancho, la aguja contina ascendiendo y el diente comienza a transportar el material.
          p.mb-3 Algunas de las operaciones que se hace en esta mquina son:
