              img.w-75.m-auto(src='@/assets/curso/temas/t1/34.svg', alt='')
            .col-sm-12.col-lg-10
              p.mb-0 La mquina fileteadora, tambin conocida como remalladora, es una herramienta esencial en la confeccin de prendas gracias al acabado profesional que proporciona. Su funcin principal es cortar, coser y filetear simultneamente, logrando costuras ms limpias y resistentes.
          .bg-color-7.p-4.mb-cus
            p.mb-0 Es til especialmente en la elaboracin de prendas de tejido de punto, como ropa interior y deportiva, ya que permite una mayor elasticidad y durabilidad en las costuras. Diseada con una estructura de cama plana, su punta sobrehilada recubre los bordes de la tela, evitando que se deshilache. Adems, incorpora una puntada de cadeneta de seguridad, ideal para prendas que requieren mayor resistencia. Para un acabado preciso, cuenta con cuchillas que cortan los extremos de la tela mientras se cose.
        .col-sm-12.col-lg-4
          .titulo-sexto.color-primario
            p.mb-0 #[b Figura 19.] Mquina  fileteadora
          figure.mb-0
            img(src='@/assets/curso/temas/t1/35.png', alt='Mquina fileteadora equipada con tres carretes de hilo amarillo, utilizada en la confeccin de ropa interior y prendas elsticas. Su funcin principal es realizar acabados de bordes con costuras resistentes y flexibles, evitando que las telas se deshilachen y proporcionando un acabado profesional.')
            figcaption.mb-0 Fuente: SENA, (2014). 


      .row.justify-content-center.mb-5
