            .tarjeta.bg-color-10
              .p-4
                p.mb-0 #[b Fileteadora de 3 hilos:] la ms sencilla es la de 3 hilos que es usada para rematar los bordes y cerrar piezas que no necesitan puntada de seguridad.


        .col-sm-12.col-lg-4.mb-cus
