              .p-4
                p.mb-0 #[b Fileteadora de 4 hilos:] esta mquina trabaja con 4 hilos y no realiza puntada de seguridad, hace doble zigzag. Es utilizada para coser material de tipo liviano como pijamas, pantys, pegar refuerzo de calzoncillos, entre otras.

        .col-sm-12.col-lg-4.mb-cus
          .tarjeta-avatar
