            .tarjeta.bg-color-10
              .p-4
                p.mb-0 #[b Fileteadora de 5 hilos:] realiza el filete o zigzag y la puntada de seguridad simultneamente ayudando a ahorrar tiempo al cerrar los costados, hombros, poner mangas, entre otras.

