          .col-sm-12.col-lg-8
            TabsA.color-acento-botones
              .tarjeta.color-acento-botones.p-4(titulo="Hilos superiores")
                h4 Hilos superiores
                p.mb-4 Los hilos superiores se enhebran parecidos a la mquina de coser plana. La mquina fileteadora puede ser trabajada con cuatro hilos; pero si es solo para filetear, se trabaja con tres de ellos, que son los hilos que realizan los orillos.
                .row.mb-0
                  .col-sm-12.col-lg-12
                    .titulo-sexto.color-primario
                      p.mb-0 #[b Figura 22.] Enhebrado de los hilos superiores (modelo MO  2410)
                    figure.mb-0
                      img(src='@/assets/curso/temas/t1/44.png', alt='Diagrama tcnico de una mquina fileteadora (overlock) modelo MO-2410, mostrando el enhebrado de los hilos a travs de los tensores y guas. Se observan cuatro carretes de hilo en la parte superior, cada uno conectado a un tensor de hilo. Desde ah, los hilos siguen un recorrido especfico a travs de varias guas hasta llegar a las agujas y el ncora de la mquina. ')
                      figcaption.mb-0 Fuente: SENA, (2014). 
              
              .tarjeta.color-acento-botones.p-4(titulo="Hilos inferiores")
                h4 Hilos inferiores
                p.mb-4 Para el enhebrado de los hilos inferiores o puntada de seguridad es un poco ms complicado. Para lograr ste enhebrado, son necesarios los otros dos que hacen la costura recta de cadeneta, la cual es llamada puntada de seguridad.
                
                .titulo-sexto.color-primario
