                figure.mb-5
                  img(src='@/assets/curso/temas/t1/45.png', alt='Mecanismo inferior de una mquina fileteadora, mostrando el sistema de alimentacin y formacin de puntada.')
                  figcaption.mb-0 Fuente: SENA, (2014). 
                p.mb-4 En el siguiente esquema, se encuentra una gua con el enhebrado de una fileteadora de 5 hilos, cada color corresponde a un hilo.
                .titulo-sexto.color-primario
                  p.mb-0 #[b Figura 24.] Enhebrado de 5 hilos 
                figure.mb-0
                  img(src='@/assets/curso/temas/t1/46.png', alt='Mecanismo inferior de una mquina fileteadora, mostrando el sistema de alimentacin y formacin de puntada.')
                  figcaption.mb-0 Fuente: SENA, (2014). 

          .col-sm-12.col-lg-4.d-none.d-lg-block
            img(src='@/assets/curso/temas/t1/43.png', alt='')

  
      .row.align-items-center.mb-4.titulo-icono(data-aos="fade-left")
        .col-auto.titulo-icono-img
          img(src='@/assets/bg/1.svg', alt='')
        .col
          h3.mb-0.titulo-icono-text.px-0.py-2 Elementos mecnicos para formar las puntadas

      p.mb-5 La siguiente figura presenta los diferentes tipos de puntadas que pueden realizar las mquinas fileteadoras, clasificadas segn la cantidad de hilos utilizados y sus elementos mecnicos. Cada tipo de puntada combina agujas, loopers y extensores para lograr distintos acabados y niveles de resistencia en la costura. Destaca la puntada de seguridad de 5 hilos, que proporciona una mayor durabilidad y es comn en la confeccin industrial.

      .titulo-sexto.color-primario
        p.mb-0 #[b Figura 25.] Elementos  mecnicos (fileteadoras)
      .row.justify-content-center.mb-5
        .col-sm-12.col-lg-8.bg-color-11.p-4
          figure.mb-0
            img.w-80.m-auto(src='@/assets/curso/temas/t1/48.svg', alt='Presenta los tipos de puntadas en mquinas fileteadoras segn la cantidad de hilos y sus elementos mecnicos. Incluye clasificaciones desde 501 (1 hilo) hasta 515 (5 hilos), detallando la combinacin de agujas, loopers y extensores utilizados en cada tipo de costura. Destaca que la puntada de 5 hilos (tipo 515) incluye dos agujas y tres loopers, formando una puntada de seguridad.')
            figcaption.mb-0.w-80.m-auto.mt-1 Fuente: SENA, (2014). 
        .col-sm-12.col-lg-4.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/47.png', alt='')
            
      .row.align-items-center.mb-4.titulo-icono(data-aos="fade-left")
