          img(src='@/assets/bg/1.svg', alt='')
        .col
          h3.mb-0.titulo-icono-text.px-0.py-2 Tipos de lubricacin y sistema de alimentacin

      .row.justify-content-center.mb-5.align-items-center
        .col-sm-12.col-lg-8
          p.mb-3 Las mquinas fileteadoras cuentan con dos tipos de lubricacin:
          .row.mb-0
            .col-sm-12.col-lg-6
              .tarjeta-avatar-b.mb-5
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/temas/t1/50.svg' alt='')
                .tarjeta.bg-color-12
                  .p-4
                    h4 Automtica
                    p.mb-0 Que puede operar mediante bombas, piones, salpique o un sistema capilar.
            .col-sm-12.col-lg-6
              .tarjeta-avatar-b.mb-0
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/temas/t1/51.svg' alt='')
                .tarjeta.bg-color-12
                  .p-4
                    h4 Manual
                    p.mb-0 En la cual el operador aplica el lubricante en los puntos clave de la mquina.
        .col-sm-12.col-lg-4.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/49.png', alt='')

      p.mb-5 En cuanto a la alimentacin, el sistema diferencial utiliza dos juegos de dientes de arrastre con transporte individual y ajuste independiente, lo que permite adaptar el avance del material segn el tipo de tela y el efecto deseado.
      .row.justify-content-center.align-items-center.mb-5.bg-color-13.px-4.py-3
        .col-sm-12.col-lg-7
            p.mb-4 El transporte diferencial se basa en la relacin entre el recorrido del diente principal y el diente diferencial. Algunos ejemplos incluyen:
            ul.lista-ul--color.mb-0
              li.d-flex
                i.fas.fa-check-circle.bg-color-1
                p.mb-0  Si el diente principal recorre 3 mm y el diente diferencial 6 mm, la relacin es 1:2, lo que acelera el avance del material.
                
              li.d-flex.mb-0
                i.fas.fa-check-circle.bg-color-1
                p.mb-0 Si el diente principal recorre 3 mm y el diente diferencial 1.5 mm, la relacin es 2:1, lo que genera un efecto de estiramiento en la tela (relacin inversa).
        .col-sm-12.col-lg-5.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/52.png', alt='')

      .bg-color-6.mb-4(data-aos="fade-left")
          h3.px-3.py-1    Tipos de alimentacin
      p.mb-5 Dependiendo del mecanismo y las necesidades de confeccin, los sistemas de alimentacin pueden ser:
      .row.mb-5
        .col-sm-12.col-lg-4.mb-cus
          .tarjeta-avatar
            img.w-25.m-auto(src='@/assets/curso/temas/t1/53.svg' alt='')
            .tarjeta.bg-color-10
              .p-4
                h4.text-center Alimentacin simple
                p.mb-0.text-center  Los dientes avanzan de manera uniforme.


        .col-sm-12.col-lg-4.mb-cus
          .tarjeta-avatar
            img.w-25.m-auto(src='@/assets/curso/temas/t1/54.svg' alt='')
            .tarjeta.bg-color-10
              .p-4
                h4.text-center Alimentacin diferencial
                p.mb-0.text-center  Permite ajustar de forma independiente el movimiento del diente principal y el diferencial.

        .col-sm-12.col-lg-4.mb-cus
          .tarjeta-avatar
            img.w-25.m-auto(src='@/assets/curso/temas/t1/55.svg' alt='')
            .tarjeta.bg-color-10
              .p-4
                h4.text-center Alimentacin asistida
                p.mb-0.text-center Utiliza rodillos o mecanismos adicionales para mejorar el arrastre de la tela.
      p.mb-5 Estos sistemas garantizan una costura precisa y adaptable a distintos tipos de tejidos, optimizando la calidad del acabado en las prendas.
      .row.align-items-center.mb-4.titulo-icono(data-aos="fade-left")
        .col-auto.titulo-icono-img
          img(src='@/assets/bg/1.svg', alt='')
        .col
