      .row.justify-content-center.mb-5
        .col-sm-12.col-lg-8
          p.mb-4 #[b Mecanismos:] el sistema de alimentacin diferencial posee dos dientes con transporte individual y ajuste independiente.
          p.mb-3 #[b Mecanismos de las mquinas fileteadoras:] las mquinas fileteadoras estn compuestas por diversos mecanismos que permiten su correcto funcionamiento.
          .bg-color-13.p-4.br-2
            p.mb-3.fw-bold Entre ellos se encuentran:
            .row.mb-0
              .col-sm-12.col-lg-7
                ul.lista-ul--color.mb-0
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo de la barra de aguja.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo del prensatelas.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo de alimentacin (oscilante sencillo con diferencial).
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo del looper derecho o superior.
                  li.d-flex.mb-0
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo del looper izquierdo o inferior.
              .col-sm-12.col-lg-5
                ul.lista-ul--color.mb-0
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo de corte.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo de lubricacin.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Mecanismo del looper (vaivn y esquive).
                  li.d-flex.mb-0
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Leva tira-hilo.
        .col-sm-12.col-lg-4.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/56.png', alt='')

      .row.justify-content-center.mb-0
        .col-sm-12.col-lg-4.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/57.png', alt='')
        .col-sm-12.col-lg-8
          p.mb-3 Adems, las mquinas cuentan con guas de hilos y tira-hilos, los cuales regulan la cantidad de hilo utilizado tanto en la aguja como en los loopers, asegurando una costura uniforme y de calidad. Cuando la mquina est equipada con puntada de seguridad (cadeneta 401), permite realizar diversas operaciones en confeccin, tales como:
          .row.justify-content-center.align-items-center.mb-3
            .col-sm-12.col-lg-6
              ul.lista-ul--color.mb-0
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Cerrar los lados de camisetas y sudaderas.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Encarterar bragas.
                  li.d-flex
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Pegar refuerzos en calzoncillos.
                  li.d-flex.mb-0
                    i.fas.fa-check-circle.bg-color-1
                    p.mb-0 Unir copas de sujetador.
            .col-sm-12.col-lg-6.d-none.d-lg-block
              img(src='@/assets/curso/temas/t1/58.png', alt='')
          p.mb-0 Estos mecanismos y funciones hacen que la fileteadora sea una herramienta esencial en la confeccin de prendas con acabados profesionales y resistentes.



      Separador
