        h2 1.3 Mquina collarn o recubridora

      p.mb-5 Elabora una puntada de cadeneta, con fines decorativos.
      .row.justify-content-center.mb-5
        .col-sm-12.col-lg-3.mb-cus
          .titulo-sexto.color-primario
