          figure.mb-0
            img(src='@/assets/curso/temas/t1/59.png', alt='Mquina collarn en funcionamiento, cosiendo una tela de color azul. Se pueden observar detalles como la aguja, las prensatelas y los hilos enhebrados, junto con un carrete de hilo azul en el fondo. La mquina realiza un acabado de sobrehilado en el borde de la tela para evitar que se deshilache.')
            figcaption.mt-1 Fuente: SENA, (2014). 
        .col-sm-12.col-lg-9
          .titulo-sexto.color-primario
