            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/t1/60.png', alt='Diagrama de una mquina collarn con sus principales componentes. Se destacan partes como la palanca tira - hilo, la barra prensatela, la barra de la aguja, el pie prensatela, el visor del aceite, el volante, las guas de hilos y los tensores del hilo.')
                figcaption.mb-0 Fuente: SENA, (2014).
            .tarjeta.bg-color-4.p-3(x="36%" y="45%" numero="")
              .h5.mb-2 Barra de la aguja
              p.mb-0 Es la que sostiene la aguja y permite el movimiento.
            .tarjeta.bg-color-4.p-3(x="36%" y="32%" numero="")
              .h5.mb-2 Barra del prensatela
              p.mb-0 Lugar donde se fija el pie del prensatela por medio de un tornillo.
            .tarjeta.bg-color-4.p-3(x="70%" y="40%" numero="")
              .h5.mb-2 Gua hilos
              p.mb-0 Son pequeos alambres que dirigen el hilo.
            .tarjeta.bg-color-4.p-3(x="36%" y="20%" numero="")
              .h5.mb-2 Palanca tira-hilo
              p.mb-0 Suministra hilo a la aguja, recupera el hilo sobrante y tensiona el hilo durante la formacin de la puntada.
            .tarjeta.bg-color-4.p-3(x="36%" y="55%" numero="")
              .h5.mb-2 Pie prensatela
              p.mb-0 La funcin es hacer presin y sostener la tela contra la planchuela para que no se mueva.
            .tarjeta.bg-color-4.p-3(x="72%" y="46%" numero="")
              .h5.mb-2 Tensores del hilo de la aguja
              p.mb-0 Tensiona el hilo para que la puntada sea correcta.
            .tarjeta.bg-color-4.p-3(x="53%" y="20%" numero="")
              .h5.mb-2 Visor del aceite
              p.mb-0 Indica el nivel de aceite que tiene la mquina.
            .tarjeta.bg-color-4.p-3(x="78%" y="30%" numero="")
              .h5.mb-2 Volante
              p.mb-0 Es la rueda que recibe el movimiento de la correa y lo transmite a los mecanismos interiores del cabezote. Tambin puede ser movido por la mano cuando se va a levantar la aguja.
              
      .row.align-items-center.mb-4.titulo-icono(data-aos="fade-left")
