              p.mb-0  Sesgar sisas de pijamas.
            li.d-flex
              i.fas.fa-check-circle.bg-color-1
              p.mb-0  Sesgar piernas de panty.
            li.d-flex.mb-0
              i.fas.fa-check-circle.bg-color-1
              p.mb-0 Collarn encauchadora o resortadora.
          .bg-color-13.p-3
            p.mb-0 Es una mquina collarn con un mecanismo llamado pullers que permite dosificar y transportar el elstico.
        .col-sm-12.col-lg-4.d-none.d-lg-block
          img(src='@/assets/curso/temas/t1/61.png', alt='')



      .bg-full-width.bg-fondo-1.mb-0.pt-5
