                .col
                  h3.mb-0.titulo-icono-text.px-0.py-2 Enhebrados de la mquina collarn

              p.mb-5 El correcto enhebrado de una mquina collarn es fundamental para garantizar una costura uniforme y sin fallos. La figura presenta el recorrido del hilo desde los carretes hasta la aguja, pasando por los tensores y guas hilos, elementos clave para mantener la tensin adecuada. Comprender este proceso es esencial para evitar problemas como roturas de hilo o puntadas irregulares, asegurando as un trabajo de calidad en la confeccin.

              .row.align-items-center.mb-4.titulo-icono(data-aos="fade-left")
                .col-auto.titulo-icono-img
                  img(src='@/assets/bg/1.svg', alt='')
                .col
                  h3.mb-0.titulo-icono-text.px-0.py-2 Elementos mecnicos para formar la puntada
              p.mb-0 Puede realizar puntadas de clase 400 y 600.
              p.mb-0 La recubridora conocida como interlock, produce una puntada que se caracteriza por ser igual en su parte superior a la que produce la mquina plana y en su parte inferior se observa una cadeneta. La puntada de cadeneta tiene la caracterstica de ser elstica. Esto mejora la calidad de algunas operaciones en los procesos de ensamble de prendas de vestir.

            .col-sm-12.col-lg-4.mb-cus
              .titulo-sexto.color-primario
