            p.mb-0 La lubricacin en estas mquinas es automtica, mientras que el sistema de alimentacin puede ser oscilante sencillo o, en algunos casos, diferencial. Se pueden utilizar hilos tanto naturales como artificiales. En cuanto a las agujas, las mquinas de cadeneta suelen emplear modelos rectos y largos, como 149 x 3, B 63, 121, UY 113 y 62 x 57.
