        h2 1.4 Mquina presilladora
      
      p.mb-4  Esta mquina pertenece a la serie de doble despunte su puntada es muy utilizada para reforzar las costuras, este tipo de mquina ha evolucionado de tal forma que ya es posible realizar una cantidad de operaciones que antes no eran posibles de realizar.
      .bg-full-width.bg-fondo-1.mb-0.pt-5
        .px-4.px-md-5.pb-md-3
