                  figure
                    img(src='@/assets/curso/temas/t1/66.png', alt='Se destacan la palanca tira - hilo, el tensor del hilo, la barra prensa tela y el pie prensatelas en la parte frontal. Tambin se sealan la gua de hilos en la parte superior, el devanador en la parte trasera, y la cama en forma de codo junto con el gancho o garfio en la parte inferior.')
